<template>
    <el-dialog title="用户信息"
               :visible="pageInfo.isShow"
               :modal-append-to-body="false"
               :close-on-click-modal="false"
               :center="true"
               :lock-scroll="false"
               @close="onClose(module_name,'pageDataRef')"
    >
        <el-form :model="pageData" :rules="pageDataRules" ref="pageDataRef" :inline="true" label-width="160px" size="small">
            <el-form-item label="用户名称" prop="userName">
                <el-input v-model="pageData.userName"></el-input>
            </el-form-item>
            <el-form-item label="用户密码" prop="password">
                <el-input type="password" v-model="pageData.password"></el-input>
            </el-form-item>
            <el-form-item label="用户昵称" prop="project_name">
                <el-input v-model="pageData.nickName"></el-input>
            </el-form-item>
            <el-form-item label="电话号码" prop="tel">
                <el-input v-model="pageData.tel"></el-input>
            </el-form-item>
            </el-form-item>
            <el-form-item label="邮箱" prop="email">
                <el-input v-model="pageData.email"></el-input>
            </el-form-item>
            <el-form-item label="备注" prop="remark">
                <el-input type="textarea" v-model="pageData.remark" style="width:696px"></el-input>
            </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
            <el-button @click="onClose(module_name,'pageDataRef')">关 闭</el-button>
            <el-button v-if="pageInfo.pageModel == 'add'" type="primary" @click="onSubmit">确 定</el-button>
            <el-button v-else-if="pageInfo.pageModel == 'edit'" type="primary" @click="onUpdate">更 新</el-button>
        </div>
    </el-dialog>
</template>
<script>
    export default {
        name: 'userManageInfo',
        data() {
            return {
                module_name: 'userManage',
                refresh_page_method: 'refresh_' + this.module_name,
                pageData: {},
                pageDataRules: {
                    userName: [{required: true, message: '请输入用户名称', trigger: 'blur'}],
                    password: [{required: true, message: '请输入新的用户密码', trigger: 'blur'}]
                }
            }
        },
        computed: {
            pageInfo() {
                return this.$setPageInfo(this.module_name)
            }
        },
        async mounted() {
        },
        methods: {
            async onSubmit() // 提交按钮
            {
                let _this = this;
                this.$refs.pageDataRef.validate(async (valid) => {
                    if (valid) {
                        let item = this.pageData;
                        let res = await _this.$insert(item, 'OAMP_insert_user_info');
                        if (res != null) {
                            _this.$message.success('新增信息成功');
                            _this.$onClose(_this.module_name, 'pageDataRef', _this.refresh_page_method);
                        } else {
                            _this.$message.error('新增信息失败');
                        }
                    }
                });
            },
            async onUpdate() // 更新按钮
            {
                let _this = this;
                this.$refs.pageDataRef.validate(async (valid) => {
                    if (valid) {
                        let item = this.pageData;
                        let res = await _this.$update(item, 'OAMP_update_user_info');
                        if (res != null) {
                            _this.$message.success('修改信息成功');
                            _this.$onClose(_this.module_name, 'pageDataRef', _this.refresh_page_method);
                        } else {
                            _this.$message.error('修改信息失败');
                        }
                    }
                });
            }
        }
    }
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    .el-input {
        width: 260px;
    }

    .el-select {
        width: 260px;
    }

    .el-date-editor.el-input, .el-date-editor.el-input__inner {
        width: 260px;
    }
</style>


